export default {
    data() {
        return {
            expressDetail: null,
            order: null,
            express: '',
            express_no: '',
            cover_pic: '',
            customer_name: '',
            is_show: false,
        }
    },
    methods: {
        getExpressDetail() {
            this.$showLoading();
            this.$request({
                url: this.$api.order.express_detail,
                data: {
                    express: this.express,
                    customer_name: this.customer_name,
                    express_no: this.express_no
                }
            }).then(response => {
                this.$hideLoading();
                this.is_show = true;
                if (response.code === 0) {
                    this.order = response.data.order;
                    this.expressDetail = response.data.express;
                } else {
                    uni.showModal({
                        title: '',
                        content: response.msg,
                        showCancel: false,
                    });
                }
            }).catch(() => {
                this.$hideLoading();
            });
        },
        copyText() {
            this.$utils.uniCopy({
                data: this.order.express_no,
                success: function () {
                    uni.showToast({
                        title: '复制成功',
                        icon: 'none',
                    })
                }
            });
        },
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.express = options.express;
        this.express_no = options.express_no;
        this.customer_name = options.customer_name;
        this.cover_pic = options.cover_pic;
        this.getExpressDetail();
    }
}
